import React from 'react';
import { connect } from 'react-redux';

import { addEntity} from '../../store/actions/entityActions';

import { Dialog } from 'primereact/dialog';
import { Divider } from 'primereact/divider';
import SaveButton from '../SaveButton';
import FileUploadSection from './FileUploadSection';

const AddMultipleItemsDialog = ({
  addEntity,
  entity,
  transaction,
  visible,
  onHide,
  isEntity,
}) => {
  const isSubmitting = isEntity ? entity.isUpdating : transaction.isUpdating;
  return (
    <Dialog
      header={`Add Multiple ${isEntity ? 'Entities' : 'Transactions'}`}
      visible={visible}
      style={{ width: '50vw' }}
      footer={<SaveButton isSubmitting={isSubmitting} />}
      onHide={() => onHide(false)}
      dismissableMask={true}
    >
      <FileUploadSection/>
      <Divider />
    </Dialog>
  );
};

const mapStateToProps = (state) => ({
  entity: state.entity,
  transaction: state.transaction,
});

export default connect(mapStateToProps, { addEntity })(AddMultipleItemsDialog);
